.mybox {
    font-family: Montserrat, sans-serif;
    height: 92vh;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }
  
  @import url("https://fonts.googleapis.com/css?family=Montserrat");
  
  
 
  
  h3 {
    margin: 10px 0;
  }
  
  h6 {
    margin: 5px 0;
    text-transform: uppercase;
  }
  
  p {
    font-size: 14px;
    line-height: 21px;
  }
  
  .card-container {
    background-color: #231e39;
    border-radius: 5px;
    box-shadow: 0px 10px 20px -10px rgba(0, 0, 0, 0.75);
    color: #b3b8cd;
    padding-top: 30px;
    padding-bottom: 30px;
    position: relative;
    width: 350px;
    max-width: 100%;
    text-align: center;
  }
  
  .card-container .pro {
    color: #231e39;
    background-color: #febb0b;
    border-radius: 3px;
    font-size: 14px;
    font-weight: bold;
    padding: 3px 7px;
    position: absolute;
    top: 30px;
    left: 30px;
  }
  
  .card-container .round {
    border: 1px solid #03bfcb;
    border-radius: 50%;
    padding: 7px;
    height: 180px;
  }
  
  
  .social-icons {
      padding: 0;
      list-style: none;
      margin: 1em;
    }
    
    .social-icons li {
      display: inline-block;
      margin: 0.15em;
      position: relative;
      font-size: 1em;
    }
  
    .social-icons svg {
      color: #fff;
      position: absolute;
      top: 0.95em;
      left: 0.96em;
      transition: all 265ms ease-out;
    }
    
  
    .social-icons a {
      display: inline-block;
    }
    
    .social-icons a:before {
      transform: scale(1);
      -ms-transform: scale(1);
      -webkit-transform: scale(1);
      content: " ";
      width: 45px;
      height: 45px;
      border-radius: 100%;
      display: block;
      background: linear-gradient(45deg, #ff003c, #c648c8);
      transition: all 265ms ease-out;
    }
    
    .social-icons a:hover:before {
      transform: scale(0);
      transition: all 265ms ease-in;
    }
  
    .social-icons a:hover svg {
      transform: scale(2.2);
      -ms-transform: scale(2.2);
      -webkit-transform: scale(2.2);
      color: #ff003c;
      background: -webkit-linear-gradient(45deg, #ff003c, #c648c8);
      background-clip: text;
      -webkit-text-fill-color: transparent;
      transition: all 265ms ease-in;
    }
    
   